import React, { FC } from 'react'
import clsx from 'clsx'
import { StopIcon } from '../../../icons/Stop'
import { Typography } from '../../Typography'
import { TabProps } from '../types'
import { Chips } from '../../Chips'

const Tab: FC<TabProps> = ({
  centerOnSelectedTab = false,
  className,
  isActive = false,
  isChips = false,
  isOutline = false,
  isDisabled = false,
  isMerged = false,
  description,
  onClick,
  chipsSize,
  size = 'L',
  title,
  id,
}) => {
  const shouldRenderDescription = description && size === 'L'
  const shouldRenderIconOnCenter = (isDisabled && !description) || size === 'S'
  const typographyType = size === 'L' ? 'Body1B' : 'Body2B'

  const onTabClick = (evt): void => {
    if (!isDisabled && onClick) {
      if (centerOnSelectedTab) {
        evt.target?.scrollIntoView({
          block: 'nearest',
          inline: 'center',
          behavior: 'smooth',
        })
      }
      onClick(id, evt)
    }
  }
  if (isChips) {
    return (
      <Chips
        id={id}
        isActive={isActive}
        isOutline={isOutline}
        isTab
        onTabSelect={onTabClick}
        size={chipsSize}
        text={title}
      />
    )
  }
  return (
    <div
      className={clsx(
        'Tabs__Tab',
        `Tabs__Tab-Size--${size}`,
        {
          '--active': isActive,
          '--disabled': isDisabled,
          '--without-desc': !description,
          '--merged': isMerged,
        },
        className,
      )}
      onClick={onTabClick}
    >
      <div className='Tabs__Tab-TitleWrapper'>
        {isDisabled && (
          <StopIcon
            className={clsx(
              'Tabs__Tab-Icon',
              shouldRenderIconOnCenter && '--centered',
            )}
            type='color'
          />
        )}
        <Typography
          className='Tabs__Tab-Title'
          text={title}
          type={typographyType}
        />
      </div>
      {shouldRenderDescription && (
        <Typography
          className='Tabs__Tab-Description'
          text={description!}
          type='Caption1Regular'
        />
      )}
    </div>
  )
}

export default Tab
